.container, .footer-container {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 1fr);
  max-width: 768px;
  place-items: center;
  row-gap: 20px;
  margin-inline: auto;
  padding: 20px 0 40px;
  display: grid;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .container, .footer-container {
    padding: 20px 15px 40px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .container, .footer-container {
    --column-count: 2;
    gap: 32px;
  }
}

@media screen and (min-width: 768px) {
  .container, .footer-container {
    place-items: start;
    padding: 60px 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container, .footer-container {
    --column-count: 3;
    max-width: 1280px;
    gap: 32px 16px;
  }
}

.is-hidden {
  visibility: hidden;
  pointer-events: none;
}

.link {
  text-decoration: none;
}

.uppercase {
  text-transform: uppercase;
}

[aria-current="page"] {
  border-bottom: 3px solid red;
}

:root {
  --FF: Roboto, sans-serif;
  --fs-800: 1.875rem;
  --fs-700: 1.25rem;
  --fs-600: 1rem;
  --fs-500: .875rem;
  --fs-400: .75rem;
  --fs-card-info: clamp(.75rem, calc(0rem + 1.56vw), 1.25rem);
  --fs-movie-modal-title: clamp(1.25rem, calc(.31rem + 1.95vw), 1.88rem);
  --fs-copyright: clamp(.88rem, calc(.79rem + .45vw), 1rem);
  --fw-500: 500;
  --fw-400: 400;
  --lh-600: 1.667;
  --lh-500: 1.34;
  --lh-400: 1.167;
  --lh-300: 1.143;
  --lh-copyright: 1.188;
}

body {
  font-family: var(--FF);
}

.header {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  flex-direction: column;
  padding: 40px 20px 92px;
  display: flex;
}

.header__main-image {
  background-image: url("home-banner.c9ac9198.png");
}

.header__library-image {
  background-image: url("library-banner.1c904152.jpg");
  background-position: 50% 100%;
  background-size: cover;
}

.header__h1 {
  font-family: #fff;
  font-size: var(--fs-800);
  font-weight: var(--fs-500);
  line-height: var(--lh-600);
  height: 35px;
  align-items: center;
  margin-left: 12px;
  display: none;
}

.header__link {
  color: #fff;
  align-items: center;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.header__main-content {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 54px;
  display: flex;
}

.header__title {
  align-items: center;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.header__navigation {
  align-items: center;
  gap: 40px;
  display: flex;
}

.header__navigation a {
  position: relative;
}

.header__navigation .active:after {
  content: "";
  height: 3px;
  width: 100%;
  background-color: red;
  position: absolute;
  bottom: -3px;
  left: 0;
}

.header__home, .header__my-library {
  color: #fff;
  font-family: #fff;
  font-size: var(--fs-400);
  font-weight: var(--fs-500);
  line-height: var(--lh-400);
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.header__home:hover, .header__my-library:hover, .header__home:focus, .header__my-library:focus {
  color: #ff6b08;
}

.header__search-input:hover, .header__search-input:focus {
  border-color: #ff6b08;
}

.header__link:hover, .header__link:focus, .header__link:hover .header__icon, .header__link:focus .header__icon {
  color: #ff6b08;
  stroke: #ff6b08;
}

.header__div-input {
  justify-content: center;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.header__icon {
  fill: #0000;
  stroke: #fff;
  width: 24px;
  height: 24px;
  margin-bottom: 5px;
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
}

.header__icon-search {
  stroke: #fff;
  fill: #0000;
  width: 12px;
  height: 12px;
  position: relative;
  top: 4px;
  right: 12px;
}

.header__search-input {
  width: 280px;
  color: #fff;
  font-family: #fff;
  font-size: var(--fs-500);
  font-weight: var(--fs-400);
  line-height: var(--lh-300);
  background: url("icons.ed5c1284.svg#icon-film_header");
