  border-bottom: .5px solid #fff;
  outline: none;
  align-self: center;
  margin-left: 12px;
  padding-right: 13px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.header__search-input::placeholder {
  color: #fff;
  font-family: #fff;
  font-size: var(--fs-500);
  font-weight: var(--fs-400);
  line-height: var(--lh-300);
